.box {
    cursor:pointer;
    width:100px;
    height:100px;
    position:absolute;
    top:20%;
    left:50%;
    transform:translateX(-50%) translateY(-50%);
    z-index: 9999;
}
.box .out {
    position:absolute;
    top:0;
    left:0;
    width:0;
    border:50px solid;
    border-radius:50%;
    border-color:transparent transparent #d72928 #db3e3d;
    transform:rotate(45deg);
    transform-origin:50px 50px;
    transition:all 0.5s;
}
.box .head {
    position:absolute;
    top:50%;
    right:18px;
    width:0;
    border:32px solid;
    border-radius:50%;
    border-color:rgb(255,255,255) rgb(248,210,69) transparent rgb(242,242,242);
    transform:translateY(-50%) rotate(-45deg);
    transform-origin:50% 50%;
    transition:all 0.5s;
}
.box .mouse {
    position:absolute;
    top:27.5%;
    right:28.5px;
    width:0;
    border:22px solid;
    border-radius:50%;
    border-color:transparent rgb(247,167,3) transparent transparent;
    transform:rotate(45deg);
    transform-origin:50% 50%;
    transition:all 0.5s;
}
.box .mouse .eye {
    position:absolute;
    top:-5px;
    left:-17px;
    width:8px;
    height:8px;
    border-radius:50%;
    transition:all 0.5s;
    background:#000;
}
.box:hover .out {
    transform:rotate(225deg);
    border-color:transparent transparent rgb(31,232,182) rgb(22,215,167);
}
.box:hover .head {
    border-color:rgb(242,242,242) transparent rgb(248,210,69) rgb(255,255,255);
    transform:translateY(-50%) rotate(-225deg);
}
.box:hover .mouse {
    transform:rotate(135deg);
}
