main {
    width: 400px;
    height: 400px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
}

main div {
    width: 150px;
    height: 150px;
    position: absolute;
    box-sizing: border-box;
    transition: 0.5s;
    background: rgba(0, 0, 0, 0.493);
    text-align: center;
    line-height: 150px;
    color: rgba(255, 255, 255, 0.849);
    font-size: 35px;
    cursor: pointer;
}

.one:hover,
.two:hover {
    box-shadow: 0 0 10px rgb(102, 98, 98);
    transform: translateY(-10px);
}

.three:hover,
.four:hover {
    box-shadow: 0 0 10px rgb(102, 98, 98);
    transform: translateY(10px);
}

.one {
    left: 0;
    top: 0;
    border-top-right-radius: 50%;
    border-bottom-left-radius: 50%;
    background: rgba(238, 10, 10, 0.644);
}

.ones {
    left: 0;
    top: 0;
    text-shadow: 5px 5px 5px rgb(56, 55, 55);
    background: rgba(238, 10, 10, 0.986);
}

.two {
    right: 0;
    top: 0;
    border-top-left-radius: 50%;
    border-bottom-right-radius: 50%;
    background: rgba(252, 235, 3, 0.863);
}

.twos {
    right: 0;
    top: 0;
    text-shadow: -5px 5px 5px rgb(56, 55, 55);
    background: rgba(252, 235, 3, 0.863);
}

.three {
    left: 0;
    bottom: 0;
    border-top-left-radius: 50%;
    border-bottom-right-radius: 50%;
    background: rgba(149, 247, 3, 0.877);
}

.threes {
    left: 0;
    bottom: 0;
    text-shadow: 5px -5px 5px rgb(56, 55, 55);
    background: rgba(149, 247, 3, 0.877);
}

.four {
    bottom: 0;
    right: 0;
    border-top-right-radius: 50%;
    border-bottom-left-radius: 50%;
    background: rgba(5, 190, 247, 0.856);
}

.fours {
    bottom: 0;
    right: 0;
    text-shadow: -5px -5px 5px rgb(56, 55, 55);
    background: rgba(5, 190, 247, 0.856);
}

.ones,
.twos,
.threes,
.fours {
    border: 3px solid white;
    color: white;
    border-radius: 80%;
}
@media screen and (max-width: 700px) {
    main {
        width: 280px;
        height: 280px;
    }

    main div {
        width: 90px;
        height: 90px;
        line-height: 90px;
        font-size: 22px;
    }

    .one:hover,
    .two:hover {
        box-shadow: 0 0 7px rgb(102, 98, 98);
        transform: translateY(-7px);
    }

    .three:hover,
    .four:hover {
        box-shadow: 0 0 7px rgb(102, 98, 98);
        transform: translateY(7px);
    }

    .one{
        left: 20px;
        top: 20px;
    }

    .two{
        right: 20px;
        top: 20px;
    }

    .three{
        left: 20px;
        bottom: 15px;
    }

    .four{
        right: 20px;
        bottom: 15px;
    }

    .ones {
        text-shadow: 3px 3px 3px rgb(56, 55, 55);
    }

    .twos {
        text-shadow: -3px 3px 3px rgb(56, 55, 55);
    }

    .threes {
        text-shadow: 3px -3px 3px rgb(56, 55, 55);
    }

    .fours {
        text-shadow: -3px -3px 3px rgb(56, 55, 55);
    }

    .ones,
    .twos,
    .threes,
    .fours {
        border: 2px solid white;
    }
}