.hello,.welcome{
    position: fixed;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
}
.hello{
    top: 50px;
}
.welcome{
    top: 75px;
}
.text_container span{
    font-size:1em;
    color:#666;
    transition: 0.25s all;
    opacity: 0;
}
.hello span {
    animation:animate 1.5s;
}
.welcome span {
    animation:animate 2s;
}
.hello span:nth-child(1) {
    animation-delay:0s;
}
.hello span:nth-child(2) {
    animation-delay:0.25s;
}
.hello span:nth-child(3) {
    animation-delay:0.5s;
}
.hello span:nth-child(4) {
    animation-delay:0.75s;
}
.hello span:nth-child(5) {
    animation-delay:1s;
}
.hello span:nth-child(6) {
    animation-delay:1.25s;
}
.welcome span:nth-child(1) {
    animation-delay:1.5s;
}
.welcome span:nth-child(2) {
    animation-delay:1.75s;
}
.welcome span:nth-child(3) {
    animation-delay:2s;
}
.welcome span:nth-child(4) {
    animation-delay:2.25s;
}
.welcome span:nth-child(5) {
    animation-delay:2.5s;
}
.welcome span:nth-child(6) {
    animation-delay:2.75s;
}
.welcome span:nth-child(7) {
    animation-delay:3s;
}
.welcome span:nth-child(8) {
    animation-delay:3.25s;
}
@keyframes animate {
    0% {
        color:#fff;
        opacity: 0;
    }
    100% {
        color: #666;
        opacity: 1;
    }
    5%{
        color: #666;
        opacity: 1;
    }
    100%{
        color:#fff;
        opacity: 0;
    }
}