.flower {
    width: 12px;
    height: 12px;
    top: -30px;
    left: 30px;
    visibility: hidden;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%
}

.flower.animated {
    -webkit-animation: flower 11s ease;
    animation: flower 11s ease;
    visibility: visible
}

.flower .flower-in {
    z-index: 2;
    width: 100%;
    height: 100%;
    border-radius: 100px;
    background-color: #de3194
}

.flower .petal {
    z-index: 1;
    width: 36px;
    height: 12px;
    background-color: #ff8cd0;
    border-radius: 50%;
    top: 0%;
    left: -100%;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%
}

.flower .petal:nth-child(2) {
    -webkit-transform: rotate(55deg);
    transform: rotate(55deg)
}

.flower .petal:nth-child(3) {
    -webkit-transform: rotate(120deg);
    transform: rotate(120deg)
}

.flower:nth-child(6) {
    top: -70px;
    left: -22px
}

@-webkit-keyframes flower {
    0%, 100% {
        -webkit-transform: scale(0);
        transform: scale(0)
    }
    20%, 80% {
        -webkit-transform: scale(.8);
        transform: scale(.8)
    }
}

@keyframes flower {
    0%, 100% {
        -webkit-transform: scale(0);
        transform: scale(0)
    }
    20%, 80% {
        -webkit-transform: scale(.8);
        transform: scale(.8)
    }
}

.rabbit {
    visibility: hidden;
    left: 278px;
    top: -10px;
    width: 20px;
    height: 50px
}

.rabbit.animated {
    -webkit-animation: rabbit 6s ease 2;
    animation: rabbit 6s ease 2;
    visibility: visible
}

.rabbit .head {
    width: 20px;
    height: 22px;
    background-color: var(--rabbit);
    border-radius: 50%
}

.rabbit .head:after {
    content: "";
    top: 8px;
    left: 5px;
    background-color: #000;
    border-radius: 50%;
    height: 3px;
    width: 3px;
    -webkit-box-shadow: 8px 0 #000;
    box-shadow: 8px 0 #000
}

.rabbit .head:before {
    content: "";
    top: 13px;
    left: 9px;
    background-color: #ff8cd0;
    border-radius: 50%;
    height: 3px;
    width: 3px
}

.rabbit .ear {
    left: 2px;
    top: -16px;
    height: 16px;
    width: 3px;
    background-color: #ff8cd0;
    border: var(--rabbit) solid 2px;
    border-radius: 50% 50% 0 0;
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg)
}

.rabbit .ear:nth-child(2) {
    left: auto;
    right: 2px;
    -webkit-transform: rotate(5deg);
    transform: rotate(5deg)
}

@-webkit-keyframes rabbit {
    0%, 80%, 100% {
        -webkit-transform: translateY(0px);
        transform: translateY(0px)
    }
    40%, 60% {
        -webkit-transform: translateY(-25px);
        transform: translateY(-25px)
    }
}

@keyframes rabbit {
    0%, 80%, 100% {
        -webkit-transform: translateY(0px);
        transform: translateY(0px)
    }
    40%, 60% {
        -webkit-transform: translateY(-25px);
        transform: translateY(-25px)
    }
}